import 'lazysizes/plugins/bgset/ls.bgset.js';
import 'lazysizes/plugins/parent-fit/ls.parent-fit.js';
// slider
import Swiper, {Navigation, Manipulation, Pagination, Keyboard, EffectFade, Autoplay, Mousewheel, EffectCreative, FreeMode, Thumbs} from "swiper";
import gsap from "gsap";
import { Draggable } from "gsap/Draggable";
import { Flip } from "gsap/Flip";

// functions
import {headerScrollChange} from "./_func/_jq/headerScrollChange";
import {getAppHeight} from "./_func/_jq/getAppHeight";
import {getScrollWidth} from "./_func/_jq/getScrollWidth";
import {menuBtn} from "./_func/_jq/menuBtn";
import {simpleModal} from "./_func/_jq/modalMagnific";
import {scrollTo} from "./_func/_jq/scrollTo";
/* ------------------- */
// preloader
var interval_index = 0;
function progress() {
    if (interval_index == 0) {
        interval_index = 1;
        var elem = $("#preloader"),
            width = 0,
            id = setInterval(frame, 22);
        function frame() {
            if (width >= 100) {
                clearInterval(id);
                interval_index = 0;
                elem.find('.preloader__percent').addClass('done');
                elem.addClass('hide').delay(500).fadeOut(500);
                $('body').removeClass('overflow');
            } else {
                width++;
                elem.find('.current-line').css('width', width + '%');
                elem.find('.preloader__percent').html(width + '%');
            }
        }
    }
}
progress();

document.addEventListener('DOMContentLoaded', () => {
    // app height
    getAppHeight();
    // scroll width
    getScrollWidth();
    // scroll header change
    headerScrollChange();
    // mobile menu
    menuBtn();

    /* animation */
    var wow = new WOW(
        {
            boxClass:     'wow',        // animated element css class
            animateClass: 'animated',   // animation css class
            offset:       200,          // distance to the element when triggering the animation
            mobile:       true,        // trigger animations on mobile devices
            live:         true,         // act on asynchronously loaded content
            callback:     function(box) {
                box.classList.add('custom-animate');
            }
        }
    );
    wow.init();

    /* main menu */
    $('.main-menu__item').hover(
        function (event){
            event.stopPropagation();
            $(this).addClass('active');
            if($(this).hasClass('submenu-item')){
                $(this).find('.submenu-wrapper').stop(true, true).slideDown(500);
            }
        },
        function (event){
            event.stopPropagation();
            $(this).removeClass('active');
            if($(this).hasClass('submenu-item')){
                $(this).find('.submenu-wrapper').stop(true, true).slideUp(500);
            }
    });
    $('.submenu-list__item').hover(
        function (event){
            event.stopPropagation();
            $(this).addClass('active');
            if($(this).hasClass('submenu-item')){
                $(this).find('.thirdmenu-wrapper').stop(true, true).show();
            }
        },
        function (event){
            event.stopPropagation();
            $(this).removeClass('active');
            if($(this).hasClass('submenu-item')){
                $(this).find('.thirdmenu-wrapper').stop(true, true).hide();
            }
    });
    /* main page */
    if($('#main-slider').length > 0){
        const workCheck = function (scroll){
            if(scroll == 0){
                mainSlider.mousewheel.enable();
            }
        }
        var mainSlider = new Swiper("#main-slider", {
            modules: [ Mousewheel ],
            direction: "horizontal",
            slidesPerView: 'auto',
            speed: 800,
            spaceBetween: 15,
            watchOverflow: true,
            init: false,
            mousewheel: {
                sensitivity: 2,
                eventsTarget: '.main-page'
            },
            on: {
                transitionEnd: function (){
                    if (mainSlider.isEnd) {
                        mainSlider.mousewheel.disable();
                        window.removeEventListener('scroll', workCheck);
                    }
                }
            },
            breakpoints: {
                1801: {
                    spaceBetween: 50,
                    direction: "vertical",
                    mousewheel: {
                        sensitivity: 2,
                        eventsTarget: '.main-page'
                    },
                },
                960: {
                    direction: "vertical",
                    mousewheel: {
                        sensitivity: 2,
                        eventsTarget: '.main-page'
                    },
                },
                576: {
                    spaceBetween: 30
                }
            }
        });
        mainSlider.init();
        $(window).scroll(function (){
            workCheck(this.pageYOffset);
        })
    }
    // news slider
    var newsSlider = new Swiper('#news-slider', {
        modules: [ Pagination ],
        speed: 800,
        loop: false,
        grabCursor: true,
        slidesPerView: 'auto',
        spaceBetween: 20,
        pagination: {
            type: "progressbar",
            el: '.swiper-pagination'
        },
        breakpoints: {
            1201: {
                slidesPerView: 4,
                spaceBetween: 20,
                pagination: false
            },
            960: {
                slidesPerView: 3,
                spaceBetween: 30,
                pagination: false
            },
            576: {
                slidesPerView: 2,
            }
        }
    });
    // events slider
    if($('.events-slider').length > 0){
        gsap.registerPlugin(Draggable, Flip);
        let wheel = document.querySelector(".wheel"),
            images = gsap.utils.toArray(".wheel__card");

        function setup() {
            let radius = wheel.offsetWidth / 2,
                center = radius,
                slice = 9,
                DEG2RAD = Math.PI / 180;
            gsap.set(images, {
                x: i => center + radius * Math.sin(i * slice * DEG2RAD),
                y: i => center - radius * Math.cos(i * slice * DEG2RAD),
                rotation: i => i * slice,
                xPercent: -50,
                yPercent: -50
            });
        }
        setup();

        window.addEventListener("resize", setup);
        Draggable.create(wheel, {
            allowContextMenu: true,
            type: "rotation",
            trigger: wheel,
            inertia: true,
            snap: {
                rotation: gsap.utils.snap(100 / images.length)
            }

        });
        gsap.to(wheel, {
            rotation: -100,
            ease: "none",
            duration: 80,
            repeat: -1
        });
    }
    /*
    var eventsSlider = new Swiper('#events-slider', {
        speed: 800,
        loop: true,
        grabCursor: true,
        centeredSlides: true,
        initialSlide: 3,
        slidesPerView: 'auto'
    });
     */

    /* restaurant sliders */
    // first slider
    var smallSlider = new Swiper("#small-slider", {
        modules: [Autoplay],
        loop: true,
        slidesPerView: 1,
        spaceBetween: 0,
        speed: 800,
        grabCursor: true,
        // effect: "creative",
        autoplay: {
            delay: 3000,
        },
        on: {
            activeIndexChange: function (){
                if(smallSlider.realIndex != bigSlider.realIndex){
                    bigSlider.slideTo(smallSlider.realIndex);
                }
            }
        }
        /*
        creativeEffect: {
            prev: {
                translate: ['-20%', 0, 0],
            },
            next: {
                translate: ['100%', 0, 0],
            },
        }
         */
    });
    var bigSlider = new Swiper("#big-slider", {
        modules: [Autoplay],
        loop: true,
        grabCursor: true,
        speed: 800,
        slidesPerView: 1,
        spaceBetween: 0,
        // effect: "creative",
        autoplay: {
            delay: 3000,
        },
        on: {
            activeIndexChange: function (){
                if(bigSlider.realIndex != smallSlider.realIndex){
                    smallSlider.slideTo(bigSlider.realIndex);
                }
            }
        }
        /*
        creativeEffect: {
            prev: {
                translate: ['-20%', 0, 0],
            },
            next: {
                translate: ['100%', 0, 0],
            },
        }
        */
    });
    function connectSlides(){
        console.log('test');
    }
    // dish slider
    var dishSlider = new Swiper('#dish-slider', {
        speed: 800,
        loop: false,
        grabCursor: true,
        slidesPerView: 'auto',
        spaceBetween: 20,
        breakpoints: {
            1440: {
                slidesPerView: 6,
            },
            1200: {
                slidesPerView: 5,
            },
            960: {
                slidesPerView: 4,
            },
            640: {
                slidesPerView: 3,
            },
            414: {
                slidesPerView: 2,
            }
        }
    });


    // footer form validate
    /*
    $('.footer .search-form').submit(function (e){
        e.preventDefault();
        console.log($(this).find('input[name="email"]').val().indexOf('@'));
        if(!$(this).find('input[name="email"]').val() == '' && $(this).find('input[name="email"]').val().indexOf('@') < 1){
            $(this).find('input[name="email"]').addClass('validate-error');
        }
        else{
            $(this).find('input[name="email"]').removeClass('validate-error');
        }
    })
     */
    /* modals */
    simpleModal();
    $(window).scroll(function() {
        var scroll = $(this).scrollTop() + $(window).innerHeight(),
            mainContent = $('.main-page').innerHeight(),
            footer = $('.footer').offset().top;
        if(scroll > mainContent && scroll < footer) {
            $('#scrollTop').show();
        } else {
            $('#scrollTop').hide();
        }
    });
    /* scroll to top */
    $("body").on('click', '#scrollTop', function(e){
        $('html,body').stop().animate({ scrollTop: 0 }, 1000);
        e.preventDefault();
    });
})